.parts{
    display: flex;
    flex-direction: row;
    gap:6rem;
    margin-left: 2rem;
    flex-wrap: wrap;

}

.partcontainer{
  flex:1;
  display: flex;
  justify-content: center;
  padding: 5px 5px 5px 5px;
}

.partContainer button{
    width: 100%;
    font-size: 10px;
    line-height: 10px;
    background: rgb(131,34,34);
    background: linear-gradient(0deg, rgba(131,34,34,1) 0%, rgba(208,21,50,1) 100%);
    padding: 0.5rem 1rem;
    color: #fff;
    font-family:var(--font-family) ;
    font-weight: 500;
    border-radius: 5px;
    line-height: 25px;
    font-size: 18px;
    border: none;
    outline: none;
    cursor: pointer;
    align-self: center;
    margin-bottom: 30px;
}

.partContainer h3{
    font-family: 'poppins';
    margin: auto;
}