@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,200;1,200&family=Raleway:wght@900&display=swap');


.contactContainer{
margin: 0;
padding: 0;
font-family: "poppins";
background: rgb(116,150,203);
background: linear-gradient(90deg, rgba(116,150,203,0.034138655462184864) 0%, rgba(116,162,235,1) 49%, rgba(242,242,242,1) 100%);
}

*{
    box-sizing: border-box;
}


form input,
textarea {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 10px auto;
  padding: 10px;
  font-family: "poppins";
  background: linear-gradient(90deg, rgba(255,255,255,0.7456232492997199) 0%, rgba(214,214,214,1) 51%, rgba(190,188,188,0.6083683473389356) 100%);
  border: 1px solid #ddd;
  justify-content: right;
  align-items: right;
  font-display: rgb(255, 252, 252);
  font-style: Bold;
  color: #fffefe;
}

form span{
    color: rgb(33, 8, 8);
    font-family: "poppins";
}

form{
    display: block;
    background:transparent;
    box-shadow: 10px 10px 15px 10px #ddd;
    margin:40px auto;
    max-width: 50%;
    padding: 20px 40px;
    
    }

    form button{
        position: relative;
        align-items: center;
        border: none;
        background: rgb(191,191,191);
        background: linear-gradient(90deg, rgba(191,191,191,1) 0%, rgba(176,176,176,1) 52%, rgba(140,140,140,1) 100%);
        margin-top: 30px;
        width:50%;
        border-radius: 5px;
        cursor: pointer;
        height: 50px;
    }


.contactdetails{
  flex: 1;
  justify-content: flex-start;
  align-items: flex-start;
  font-family: "poppins";
  margin-left: 3rem;
}

.contactdetails h2{
  text-align: center;
  font-size: 30px;
  color: #890808;
}
