  .productnav {
    background-color:transparent;
    display: flex;
    justify-content: center;   
    align-items: center;
    padding: 1rem 2rem;
    margin-bottom: 2rem;
    font-family: 'poppins';
  }
  
  .menu-icon {
    display: none;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 1.5rem;
    color: #000000;
  }
  
  .nav-menu {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
  }
  
  .nav-item {
    margin-right: 1rem;
  }
  
  .nav-links {
    color: #000000;
    text-decoration: none;
    padding: 1rem 1rem;
    font-weight: 500;
    font-size: large;
    transition: all 0.3s ease-in-out;
  }
  
  .nav-links:hover {
    color: #c40606;
  }
  
  .nav-links.activated {
    color: #a10000;
    border-bottom: 2px solid #870000;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      background: #19191a;
      left: 0;
      opacity: 1;
      transition: all 0.6s ease;
      z-index: 1;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .nav-links:hover {
      color: #6a0000;
      transform: scale(1.2);
      transition: all 0.3s ease;
    }
  
    .nav-item:hover {
      border: none;
    }
  
    .nav-item {
      width: 100%;
    }
  
    .navbar-logo {
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }

    
  }