.footerbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 4rem;
    margin-left: 400px;
    margin-bottom: 20px;
    margin-top: 200px;
  }
  
  .footerbar-container {
    width: 100%;
    max-width: 1140px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .footerbar-links_logo h2 {
    color: #000000;
    font-size: 2rem;
    font-weight: bold;
    margin: 0;
  }
  
  .menu-icon {
    font-family: popins;
    display: none;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    font-size: 1.5rem;
    color: #000000;
  }
  
  .footer-menu {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
  }
  
  .footer-item {
    margin-right: 1rem;
  }
  
  .footer-links {
    font-family: poppins;
    color: #000000;
    text-decoration: none;
    padding: 1rem 1rem;
    font-weight: 500;
    font-size:larger;
    border-left: 1px solid rgb(51, 0, 0);
  }

  .footer-menu > .footer-item:first-child .footer-links {
    border-left: 3px solid transparent; 
  }
  
  .footer-links:hover {
    color: #c40606;
  }
  
  .footer-links.activated {
    color: #a10000;
  }
  
  .icons {
    display: flex;            
    align-items: center;
  }
  
  .a, .b, .c, .d {
    width: 40px;
    height: 40px;  
    margin-top: 20px;            
    margin-right: 20px;   
    margin-bottom: 20px;
    fill:#616161;
    fill: linear-gradient(90deg, rgba(185,185,185,1) 0%, rgba(176,176,176,1) 52%, rgba(201,198,198,1) 100%);          
    transition: transform 0.2s ease-in-out; 
  }

  .a{
    margin-left: 45%;
  }
  @media screen and (max-width: 960px) {
    .footerbarItems {
      position: relative;
    }
  
    .footer-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      position: relative;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .footer-menu.active {
      background: #19191a;
      left: 0;
      opacity: 1;
      transition: all 0.6s ease;
      z-index: 1;
    }
  
    .footer-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
  
    .footer-links:hover {
      color: #6a0000;
      transform: scale(1.2);
      transition: all 0.3s ease;
    }
  
    .footer-item:hover {
      border: none;
    }
  
    .footer-item {
      width: 100%;
    }
  
    .footerbar-logo {
      position: relative;
      top: 0;
      left: 0;
      transform: translate(25%, 50%);
    }
  
    .menu-icon {
      display: block;
      position: relative;
      top: 0;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }

    
  }